function toString(x) {
  if (typeof x === 'string') {
    // return x;
    return `[[;gold;]"${ x }"]`;
  } else if (x instanceof Cmd) {
    var s = x.f();
    if (s) { return `[[;gold;]"${ s }"]` }
  } else if (!isNaN(x)) {
    return `[[;mediumorchid;]${ String(x) }]`;
  } else if (Array.isArray(x)) {
    if (x.length > 10) {
      return "[[;limegreen;]]" + x.slice(0, 10).map(toString).join("[[;limegreen;], ]") + "[[;limegreen;], ]...[[;limegreen;]]"
    } else {
      return "[[;limegreen;]]" + x.map(toString).join("[[;limegreen;], ]") + "[[;limegreen;]]";
    }
  } else if (typeof x === 'object') {
    try {
      // return String(R.map(n => n.id(), x));
      if (x.id()) {
        return `[[;orange;]{]G(${ x.id() })[[;orange;]}]`;
      } else {
        return "[[;orange;]{]...[[;orange;]}]";
      }
    } catch (e) {
      return "[[;orange;]{]...[[;orange;]}]";
    }
  } else if (typeof x === "function") {
    return `[[;royalblue;](${ x.name })]`;
  } else {
    return String(x);
  }
}

class Terminal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {command: ''};
  }
  update(command) {
    this.setState({
      command
    });
  }
  componentDidMount() {
    var {interpreter, ...options} = this.props;
    if (!options.onCommandChange) {
      options.onCommandChange = (command) => this.update(command);
    }
    interpreter = interpreter || function(command, term) {
      if (command !== '') {
        try {
          var result = window.eval(command);
          if (result !== undefined) {
            var s = toString(result);
            if (s !== undefined) { term.echo(s); }
          }
        } catch(e) {
          term.error(String(e));
          // throw(e);
        }
      } else {
        term.echo('');
      }
    };
    this.terminal = $(this.node).terminal(interpreter, options);
  }
  componentWillUnmount() {
    this.terminal.destroy();
  }
  isCommandControlled() {
    return this.props.command !== undefined;
  }
  render() {
    return (
      <div ref={(node) => this.node = node}></div>
    );
  }
}

export default Terminal;
