const Flavor = Object.freeze({
  CUT:    Symbol("CUT"),
  DIFF:   Symbol("DIFF"),
  CHOP:   Symbol("CHOP")
});
class Ind {
  constructor(i=0, flavor=Flavor.ORD) {
    this.i = i;
    this.flavor = flavor;
    this.str = i.toString();
    switch (flavor) {
      case Flavor.CUT:    this.str =  "("+this.str+")-";  break;
      case Flavor.DIFF:   this.str =  "+"+this.str+"+";   break;
      case Flavor.CHOP:   this.str = "-("+this.str+")";   break;
    }
  }
  toString() { return this.str; }
}
function icut(i)  { return new Ind(i, Flavor.CUT); }
function idiff(i) { return new Ind(i, Flavor.DIFF); }
function ichop(i) { return new Ind(i, Flavor.CHOP); }
function iscut(i)  { return i.flavor == Flavor.CUT; }
function isdiff(i) { return i.flavor == Flavor.DIFF; }
function ischop(i) { return i.flavor == Flavor.CHOP; }

export const zero = idiff(0);

function add(i, j) {
  switch (i.flavor) {
    case Flavor.CUT: 
      switch (j.flavor) {
        case Flavor.CUT:  return icut(j.i + i.i);
        case Flavor.DIFF: 
          if (i.i > j.i)  return icut(i.i - j.i);
          if (i.i < j.i)  return idiff(j.i - i.i);
          return zero;
        case Flavor.CHOP: break;
      }
    case Flavor.DIFF:
      switch (j.flavor) {
        case Flavor.CUT:  break;
        case Flavor.DIFF: break;
        case Flavor.CHOP: 
          if (i.i > j.i)  return idiff(i.i - j.i);
          if (i.i < j.i)  return ichop(j.i - i.i);
          return zero;
      }
    case Flavor.CHOP:
      switch (j.flavor) {
        case Flavor.CUT:  break;
        case Flavor.DIFF: break;
        case Flavor.CHOP: return ichop(i.i + j.i);
      }
  }
  throw `can't add: ${i} + ${j}`;
}

