class Shift extends T.Term {
  constructor(ind, val) {
    super("shift", val);
    this.ind = ind;
    this.val = val;
  }
  getData() { return {color: T.COLORS.red, shape: "diamond", name: null}; }
  getEdgeData(i) { return {edgename: this.ind}; }
  toString() { return `(${this.ind} ${this.val})`; }
  // hash() { return T.make_str("shift", this.ind, this.val.hash0()); }
  hash() { return T.make_uni(this.con, ...this.args)+this.ind; }
  copy(val) { return new Shift(this.ind, val); }
}

class Clos extends T.Term {
  constructor(f, i, arg, j=0) {
    super("clos", f, arg);
    this.f = f;
    this.arg = arg;
    this.inds = [idiff(i), idiff(j)];
    console.log(this.con);
    console.log(this.args);
  }
  getData() { return {color: T.COLORS.blue, shape: "triangle", name: null}; }
  getEdgeData(i) { return {edgename: this.inds[i]}; }
  hash() { return T.make_uni(this.con, ...this.args)+this.inds; }
  copy(f, arg) { return new Clos(f, this.inds[0].i, arg, this.inds[1].i); }
}

class Link extends T.Term {
  constructor(term, ind, next) {
    super("link", term, next);
    this.ind = idiff(ind);
  }
  getEdgeData(i) { return i ? {edgename: this.ind} : {edgename: null, identify:true}; }
  getData() { return {color: T.COLORS.gray, shape: "rhomboid", name: null}; }
  hash() { return T.make_str("link", this.ind, this.next.hash0()); }
  copy(term, next) { return new Link(term, this.ind.i, next); }
}
class Var extends T.Term {
  constructor() { super("var"); }
  getData() { return {color: T.COLORS.blue, shape: "vee", name: null}; }
  // hash() { return "(var)"; }
  copy() { return this; }
  toString() { return "v"; }
}

export const VAR = new Var();

export function shift(i, s)             { return new Shift(i, s); }
export function cut(i, s)               { return shift(icut(i),  s); }
export function diff(i, s)              { return shift(idiff(i), s); }
export function chop(i, s)              { return shift(ichop(i), s); }
export function zord(s)                 { return diff(zero, s); }
export function clos(f, i, arg, j=0)    { return new Clos(f, i, arg, j)}
export function app(f, arg)             { return cut(1, clos(f, 1, arg)); }
export function v(i)                    { return chop(i, VAR); }
export function link(term, i, next)     { return new Link(term, i, next); }
export function chain(...args) {
  return args.length == 1 ? args[0] : link(args[0], args[1], chain(args.slice(2)));
} 

function siscut(s)  { return s instanceof Shift && iscut(s.ind); }
function sisdiff(s) { return s instanceof Shift && isdiff(s.ind); }
function sischop(s) { return s instanceof Shift && ischop(s.ind); }

