window.merge = function(n) {
  let t1 = window.t(n);
  let i = t1.ind;
  let t2 = t1.val;
  let j = t2.ind;
  let t3 = t2.val;
  let k = add(i, j);
  let t4 = shift(k, t3);
  let t5 = T._truth(T.eq(t1, t4));
  window.echo(t5);
  window.B.add(t5);
}



