export const COLORS = {
  blue:   "#11479E", 
  black:  "#0A1128", 
  gray:   "#586F7C", 
  gold:   "#EAC435", 
  red:    "#E40066"
};

export function make_str(con, ...args) { return "("+con+" "+args.map(String).join(" ")+")"; }
export function make_uni(con, ...args) { return make_str(con, ...args.map(a => a.hash0())); }

//// TERMS ////

export class Term {
  // constructor does just enough to compute uni and save args
  constructor(con, ...args) {
    this.con = con;
    this.args = args;
    this.save = false; 
  }

  hash0() { return `@${this.id}`; }
  hash() { return make_uni(this.con, ...this.args); }
  getData() { return {}; }
  getEdgeData(i) { return {}; }

  toString() { return make_str(this.con, ...this.args); }
  // toString() { return this.hash(); }
  // fullString() { return make_str(this.con, ...this.args); }
  flash() { window.flash(this.node); }
  copy(...args) { return new this.constructor(this.con, ...args); }
}

export class App extends Term {
  getData() { return {color: COLORS.blue, shape: "triangle", 'text-margin-y': '5px'}; }
}

class Judg extends Term {
  getData() { return {color: COLORS.gray, shape: "diamond"}; }
}

class Quote extends Judg {
  constructor(arg, ord=1) {
    super("quote", arg);
    this.ord = ord;
    this.val = arg;
  }
  getData() { return {name: `+${this.ord}+`, ...super.getData()}; }
  getEdgeData(i) { return {edgename: null}; }
  copy(...args) { return new this.constructor(...args); }
}

class Prim extends Term {
  constructor(val, f=null) { 
    super("prim");
    this.val = val;
    this.f = f;
  }
  // hash0() { return this.val; }
  hash() { return this.val; } // Math.random(); }
  toString() { return String(this.val); }
  getData() { return {name: this.val, color: COLORS.black}; }
  copy(...args) { return new this.constructor(...args); }
}

class Type extends Term {
  constructor(type) { 
    super("type");
    this.val = type;
  }
  hash() { return this.val; }
  toString() { return this.val; }
  getData() { return {name: this.val, color: COLORS.red}; }
  copy(...args) { return new this.constructor(...args); }
}

class Truth extends Term {
  constructor(arg) { 
    super("truth", arg);
    this.val = arg;
  }
  getData() { return {name: "", color: COLORS.gold, shape: "star", 'text-margin-y': '2px'}; }
  getEdgeData(i) { return {edgename: null}; }
  toString() { return `$${this.val}`}
  copy(...args) { return new this.constructor(...args); }
}

export function prim(...args)   { return new Prim(...args ); }
export function type(...args)   { return new Type(...args ); }
export function quote(...args)  { return new Quote(...args); }
export function prop(...args)   { return new Prop(...args); }

export function term(...args)   { return new App(...args ); }
export function plus(...args)   { return new App("+", ...args ); }
export function mul(...args)    { return new App("x", ...args ); }
export function ifte(...args)   { return new App("?:", ...args ); }

export function step(...args)   { return new Judg("", ...args ); }
export function steps(...args)  { return new Judg("*", ...args ); }
export function bstep(...args)  { return new Judg("", ...args ); }
export function isa(...args)    { return new Judg(":", ...args ); }
export function eq(...args)     { return new Judg("=", ...args ); }
export function norm(...args)   { return new Judg("", ...args ); }
export function top(...args)    { return new Judg("", ...args ); }

// "private"
export function _truth(...args) { return new Truth(...args); }
export function _mv(...args)    { return new MVar(...args); }

export const WILD = new U.Unique("WILD");

//// PATTERNS ////

class MVar extends Term {
  constructor(name, arg=null, pred=null) {
    super("MV");
    switch (typeof pred) {
      case "string":
        this.pred = (t => t.con === pred); break;
      case "function":
        this.pred = pred; break;
      default:
        this.pred = (()=>true);
    }
    this.name = name;
    this.val = arg;
  }
  hash() { return this.arg ? make_uni("@"+this.name, this.arg) : "@"+this.name; }   // should use pred too
  getData() { return {name: this.name, color: COLORS.gray, shape: "vee"}; }
  toString() { return `@${this.name}`; }
  copy(...args) { return new this.constructor(...args); }
}
class Prop extends Term {
  constructor(arg) { 
    super("prop", arg);
    this.val = arg;
  }
  getData() { return {
    name: "", shape: "star", 'text-margin-y': '2px',
    color: COLORS.black, 'border-color': COLORS.gold }; }
  getEdgeData(i) { return {edgename: null}; }
  toString() { return `?${this.val}`}
  copy(...args) { return new this.constructor(...args); }
}

export class Rule {
  constructor(name, prems, conc, remove=null) {
    this.name = name;
    this.prems = prems;
    this.conc = conc;
    this.remove = remove || [];
  }
}

