//// BUILDER ////

window.K = "K";
window.E = "E";
window.V = "V";
window.sticky = 0.4;

export class Builder {
  constructor(cy, next) {
    this.cy = cy;
    this.next = next;
    this.STORE = {};
    this.run();

    window.test = U.cmd(() => this.test());
    // window.send('test');
  }
  add(term, save=true) {
    if (!term) { return; }
    term.save |= save;

    // make children
    term.args = term.args.map(arg => this.add(arg, false));

    // cons hashing
    var uni = term.hash();
    if (uni in this.STORE) { 
      term = this.STORE[uni];
      term.save |= save; 
      this.cy.add(term.node);
      term.edges.map(e => this.cy.add(e));
      return term;
    }
    this.STORE[uni] = term;

    // build term
    term.id = this.next();
    var data = {
      term,
      id: term.id, name: term.con,
      forceStyle: true,
      ...term.getData()
    }
    
    window.batch();
    // term.node = this.cy.add({data});
    let ext = this.cy.extent();
    term.node = this.cy.add({data, position: {
      x:(ext.x1+ext.x2)/2+Math.random()*ext.w/10, // y:ext.y2-ext.h/10}});
      y:(ext.y1+ext.y2)/2+Math.random()*ext.h/10}});

    // connect children
    term.edges = [];
    for (var [i, arg] of term.args.entries()) {
      // console.log(arg);
      var edgeData = {
        id: this.next(), edgename: i,
        source: term.node.id(), 
        target: arg.node.id(),
        width: 2,
        ...term.getEdgeData(i)
      }
      term.edges.push(this.cy.add({data: edgeData}));
    }

    return term;
  }
  r(node, force=true) {
    node = n(node);
    if (!node) { return; }
    var term = t(node);
    if (force) { term.save = false; }
    if (!term.save && node.incomers('node').length == 0) {
      var outgoers = node.outgoers('node');     // store outgoers but don't recur yet
      this.cy.remove(node);                     // remove node
      outgoers.map(out => this.r(out, false));  // okay now recur
    }
  }
  rs(...nodes) { nodes.map(n => this.r(n)); }
  tick() { 
    const mul = 1;
    var nodes = this.cy.nodes().filter(n => n.visible());
    var edges = this.cy.edges().filter(n => n.visible());
    let ext = this.cy.extent();
    let cx = (ext.x1+ext.x2)/2;
    let cy = (ext.y1+ext.y2)/2;
    nodes.map(n => {
      n.d = {x:0, y:0};
      n.p = n.position();
    });
    nodes.map(n => {
      var args = t(n).args;
      for (var i=0; i<args.length-1; i++) {
        var a = args[i].node[0];
        var b = args[i+1].node[0];
        let dx = b.p.x - a.p.x;
        // push first sibling 80 left of next
        let force = (dx - 80)/(10+0*i);
        a.d.x += force*mul;
        b.d.x -= force*mul;
      }
      nodes.map(n2 => {
        if (n == n2) { return; }
        let dx = n2.p.x - n.p.x;
        let dy = n2.p.y - n.p.y;
        let d = Math.sqrt(dx*dx + dy*dy);
        // but push close nodes apart // pull pairs of nodes together
        let force = (d<80 ? (80-d)/20 : 0);// - d/2000;
        n.d.x -= dx/d*force*mul;
        n.d.y -= dy/d*force*mul;
        n2.d.x += dx/d*force*mul;
        n2.d.y += dy/d*force*mul;
      });
      // // push roots to y=0
      // if (!n.incomers().length) {
      //   let force = n.p.y/10;
      //   n.d.y -= force*mul;
      // }
    });
    edges.map(e => {
      var s = e.source();
      var t = e.target();
      let dy = t.p.y - s.p.y;
      let dx = t.p.x - s.p.x;
      // push target 80 below source
      let forcey = (dy - 80)/10;
      s.d.y += forcey*mul;
      t.d.y -= forcey*mul;
      // pull connected nodes together 
      let forcex = dx/80;
      s.d.x += forcex*mul;
      t.d.x -= forcex*mul;
    })
    nodes.map(n => {
      if (n.d.x*n.d.x + n.d.y*n.d.y > window.sticky) {
        n.shift(n.d);
      }
    });
    this.cy.fit(nodes, 20);
  }
  run() { 
    setInterval(this.tick, 50);
  }
  test() {
    let a =   this.add(prop(plus(_mv('x'), plus(prim(13), prim(14)))));
    let b =   this.add(_truth(plus(plus(prim(11), prim(12)), _mv('y'))));
    let M =   this.unify(a, b);
    // // let res = this.add(this.replace(a, M));
    console.log(String(a));
    console.log(String(b));
    console.log(M);

    // let x = plus(prim(10), prim(20));
    // this.add(x);
    // this.r(x);
    // return this.add(x);
  }

  //// REWRITING ////

  match(patterns, ...terms) {
    var M = {"m":1};
    function match(pattern, term) {
      // console.log(String(pattern), String(term));
      if (pattern === term) { return true; }
      switch (pattern.con) {
        case "MV":
          if (pattern.name in M) { return M[pattern.name] == term; }
          M[pattern.name] = term;
          if (!pattern.pred(term)) { return false; }
          if (pattern.val) { return match(pattern.val, term); }
          return true;
        case "prim":
        case "type":
          return term.con == pattern.con && (term.val == pattern.val || pattern.val == WILD);
        case "prop":
          return (term.con == "truth" || term.con == "prop") && match(pattern.val, term.val);
        default:
          if (pattern.con !== term.con) { return false; }
          if (pattern.args.length !== term.args.length) { return false; }
          return Object.entries(pattern.args).every(([i,a]) => match(a, term.args[i]));
      }
    }
    if (!Array.isArray(patterns)) { patterns = [patterns] }
    if (patterns.length != terms.length) { return false; }
    if (U.allIndexed( ([p,t],i) => match(_mv("arg"+i, p), t), R.zip(patterns, terms) )) { return M; }
  }
  unify(t0, t1, M=null) {
    let next = this.next;
    // {[term,(0|1|2)] : term}
    if (M) {
      M = U.dmap(M, R.identity);
    } else {
      M = {"u":1};
    }
    function union([t0,l0], [t1,l1]) {
      M[[t0.hash(),l0]] = [t1,l1];
      return [t1,l1];
    }
    function find(x) {
      if (x in M) {
        let xhash = [x[0].hash(),x[1]];
        M[xhash] = find(M[xhash]);
        return M[xhash];
      }
      return x;
    }
    function val(p) { return [p[0].val, p[1]]; }
    function args(p) { return R.map(a => [a,p[1]], p[0].args); }
    function unify(p0, p1) {
      [p0, p1] = [find(p0), find(p1)];  // get roots of union trees
      if (p0 === p1) { return true; }   // if same then done early
      let [[t0,l0],[t1,l1]] = [p0,p1];  // extract terms and labels
      if (t0 === null || t1 === null) { return true; }  // happens if val of MV is null

      if (t0.con == t1.con) {
        let con = t0.con;
        if (con == "MV") { union(p0, union(p1, [_mv(`~${next()}~`), 2])); return unify(val(p0), val(p1)); }
        if (con == "prim" || con == "type") { 
          return (t0.val == WILD) || (t1.val == WILD) || (t0.val == t1.val);
          // if (t0.val == WILD) { union(p0, p1); return true; }
          // if (t1.val == WILD) { union(p1, p0); return true; }
          // return t0.val == t1.val; 
        }
        return R.all( ps => unify(...ps) , R.zip(args(p0), args(p1)) );
      } else {

        if (t0.con == "MV") { union(p0, p1); return unify(val(p0), p1); }
        if (t1.con == "MV") { union(p1, p0); return unify(val(p1), p0); }
        if (t0.con == "prop" && t1.con == "truth") {
          union(p0, p1);
          return R.all( ps => unify(...ps) , R.zip(args(p0), args(p1)) ); 
        }
        if (t1.con == "prop" && t0.con == "truth") {
          union(p1, p0);
          return R.all( ps => unify(...ps) , R.zip(args(p0), args(p1)) ); 
        }
      }
    }
  
    if (!Array.isArray(t0)) { t0 = [t0] }
    if (!Array.isArray(t1)) { t1 = [t1] }
    if (t0.length != t1.length) { return false; }
    // if (U.allIndexed( ([t,s],i) => unify([_mv("arg"+i, t),0], [_mv("arg"+i, s),1]), R.zip(t0, t1) )) { return M; }
    if (R.all( ([t,s]) => unify([t,0], [s,1]), R.zip(t0, t1) )) { return M; }
  }

  replace(t, M, maketruth=false, label=0) {
    function find(x) {  // THIS IS COPY/PASTED FROM UNIFY :(
      if (x in M) {
        let xhash = [x[0].hash(),x[1]];
        M[xhash] = find(M[xhash]);
        return M[xhash];
      }
      return x;
    }
    function args(p) { return R.map(a => [a,p[1]], p[0].args); }
    function ureplace(p) {
      p = find(p);
      let [t,l] = p;
      switch (t.con) { 
        case "MV":    // already did find so p is root and still free in result
        case "type":  return t;
        case "prim":
          if (t.f) {  
            let val = t.f(mv => M[[mv,l]][0].val);      // pass lookup function
            if (val instanceof Term) { return val; }
            return prim(val); 
          }  
          return t;
        case "prop":  return (maketruth ? _truth : prop)(...args(p).map(ureplace));
        default:      return t.copy(...args(p).map(ureplace));
      }
    }
    function replace(conc) {
      switch (conc.con) { 
        case "MV":    return M[conc.name];
        case "prim":
          if (conc.f) { return conc.copy(conc.f( U.dmap(M, ([k,v]) => [k,v.val]) )); }
          return conc;
        case "type":  return conc;
        case "prop":  return (maketruth ? _truth : prop)(replace(conc.val));
        default:      return new conc.constructor(conc.con, ...conc.args.map(replace));
      }
    }
    if (!M)  { return; }
    if (M.u) { 
      // if (t instanceof Rule) { 
      //   return new Rule(t.name+"'", t.prems.map(p => ureplace([p,label])), ureplace([conc,label]), t.remove);
      // }
      return ureplace([t,label]); }
    if (M.m) { 
      // if (t instanceof Rule) { 
      //   return new Rule(t.name+"'", t.prems.map(p => ureplace([p,label])), ureplace([conc,label]), t.remove);
      // }
      return replace(t); 
    }
  }

  use(rule, ...prems) {
    prems = prems.map(t);
    if (prems.length == rule.prems.length) {            // desired conc *not* provided
      let M = this.match(rule.prems, ...prems);
      if (M) { return this.replace(rule.conc, M, true); }
    } else if (prems.length == rule.prems.length+1) {   // desired conc *is* provided
      let M = this.unify([rule.conc].concat(rule.prems), prems);
      if (M) { return this.replace(rule.conc, M, true); }
    }
  }

  prove(goal) {
    for (var rule of window.RULES) {
      var M = this.unify(rule.conc, goal);
      if (M) {  // this rule can build the goal
        var subgoals = rule.prems.map(p => this.replace(p, M));
        var truths = subgoals.map(g => this.prove(g));
        if (truths.every(R.identity)) {
          return this.use(rule, goal, ...truths);
        }
      }
    }
  }
  reduce(node) {
    return this.add(this.prove(prop(bstep(t(node), _mv('res')))));
  }

  aprove(goal, delay=1000) {
    let thiz = this;
    let stack = [];
    function push(goal) {
      let frame = {goal};
      let {rules, truth} = thiz.iprove("V", goal)[0];   // break out subgoals
      frame.rules = rules.map(rule => ({truths: [], ...rule}));
      frame.truth = truth;
      stack.push(frame);
    }
    function pop() {
      var {rules, truth} = stack.pop();
      rules.map(({subgoals}) => subgoals.map(s => thiz.r(thiz.add(s))));
      if (stack.length) {
        let {rules} = stack[stack.length-1];
        if (truth) {  // success
          rules[0].truths.push(truth); 
        } else {      // fail
          let {subgoals} = rules.shift();
          subgoals.map(s => thiz.r(s));
        }
      }
    }
    function step() {
      while(stack.length) {
        // console.log(stack);
        let frame = stack[stack.length-1];
        let {goal, rules, truth} = frame;
        if (!rules.length || truth) { // pop
          pop();
        } else {
          let {rule, subgoals, truths} = rules[0];
          if (subgoals.length == truths.length) {  // all truths found
            frame.truth = this.iprove("V", rule, goal, ...truths)[0].truth;   // build up truth
          } else {  // more work to do
            let subgoal = subgoals[truths.length];
            push(subgoal);
            window.setTimeout(step, delay);
            break;
          }
        }
      }
    }
    push(goal);
    window.setTimeout(step, delay);
  }
  areduce(node, delay=1000) {
    let goal = this.add(prop(bstep(t(node), _mv('res'))));
    window.setTimeout(() => this.aprove(goal, delay), delay);
    window.echo(`[[;gold;]"Animatedly proving [[;royalblue;]${str(goal)}]"]`);
  }

  ireduce(node) {
    let goal = this.add(prop(bstep(t(node), _mv('res'))));
    window.echo(`[[;gold;]"Interactively proving [[;royalblue;]${str(goal)}]"]`);
  }

  iprove(...args) { 
    args = args.map(t);
    let goals = args.filter( x => x instanceof Prop  );
    let prems = args.filter( x => x instanceof Truth ); // TODO: allow non-truths?
    let rules = args.filter( x => x instanceof Rule  );
    // if (!rules.length && goals.length) { rules = window.RULES; }  // default to all rules when goal-directed
    if (!rules.length) { rules = window.RULES; }  // default to all rules when goal-directed

    let all = Object.values(this.STORE).filter(t => n(t).visible());
    let allprems = all.filter(t => t instanceof Truth && !(t in prems));
    let allgoals = all.filter(t => t instanceof Prop  && !(t in goals));

    let K = args.includes("K");
    let toRemove = [];
    function rem(t) { if (!K && !toRemove.includes(t)) { toRemove.push(t); } }

    // let E = args.includes("E");
    function log(x)  { if (false) { console.log(x); } return x; }
    function echo(x) { if (true) { window.echo(`[[;gold;]"${x}"]`); } return x; }

    log("--- iprove ---");  

    let v = args.includes("V");
    let allToAdd = [];

    for (let goal of goals) {
      log(`Goal: ${String(goal)}`);
      let toAdd = [];
      let proved = false;
      for (let rule of rules) {
        log(`Rule: ${rule.name}`);
      
        var M = this.unify(rule.conc, goal);
        if (!M) { continue; }

        let used = [];
        let rprems = rule.prems.filter(rp => {
          let [p, M2] = U.find(prems, p => this.unify(rp, p, M));       // prems
          if (p) { used.push(p); M = M2; }
          return !p;
        }).filter(rp => {
          let [p, M2] = U.find(allprems, p => this.unify(rp, p, M));  // allprems
          if (p) { used.push(p); M = M2; }
          return !p;
        });
        
        if (rprems.length) {
          toAdd.push({rule, subgoals: rprems.map(rp => this.replace(rp, M))});
        } else {
          let ret = this.add(this.replace(rule.conc, M, true));
          echo(`Proved [[;royalblue;]${str(ret)}] with [[;mediumorchid;]${rule.name}]`);
          rem(goal); 
          used.map(rem);
          if (v) { allToAdd.push({goal, rules: [], truth: ret}); }
          proved = true;
          break; // don't try any more goals, on to next goal
        }
      }
      if (!proved) {
        toAdd.map(({rule, subgoals}) => {
          echo(`Added subgoals to prove [[;royalblue;]${String(goal)}] with [[;mediumorchid;]${rule.name}]`);
          subgoals.map(t => this.add(t));
        });
        if (v) { allToAdd.push({goal, rules: toAdd}); }
      }
    }
    if (!goals.length) {
      log(`No Goal`);
      for (let rule of rules) {
        log(`Rule: ${rule.name}`);
        if (rule.prems.length != prems.length) { continue; }
        var M;
        for (let [rp, p] of R.zip(rule.prems, prems)) {
          M = this.unify(rp, p, M);
          if (!M) { continue; }
        };
        log(String(rule.conc));
        log(M);
        let ret = this.add(this.replace(rule.conc, M, true));
        if (ret) {
          echo(`Proved [[;royalblue;]${str(ret)}] with [[;mediumorchid;]${rule.name}]`);
          // prems.map(rem);
          // allgoals.filter(g => this.match(g, ret)).map(rem);
        }
      }
    }
    
    toRemove.map(t => this.r(t));
    if (v) { return allToAdd; }
  }

  iequate(node) {
    let goal = this.add(prop(eq(t(node), _mv('res'))));
    window.echo(`[[;gold;]"Interactively proving [[;royalblue;]${str(goal)}]"]`);
  }

  isteps(node) {
    let goal = this.add(prop(steps(t(node), _mv('res'))));
    window.echo(`[[;gold;]"Interactively proving [[;royalblue;]${str(goal)}]"]`);
  }

}

//// EXPORTS ////

// (term|node -> term)
function t(x) {
  // if (x instanceof Term) { return x; }
  if (x.data) { return x.data('term'); }
  return x
}
// (term|node -> node)
function n(x) {
  if (x instanceof Term) { return x.node; }
  return x;
}
// (term|node -> [term,node])
function tn(x) {
  return [t(x), n(x)];
}
window.t = t;
window.n = n;
window.tn = tn;

// var t = window.t = (n => n.data('term'));
var c = window.c = (n => t(n).con);
// var v = window.v = (n => t(n).val);
// var n = window.n = (n => n.data('name'));
var str = window.str = (n => String(t(n)));
var u = window.u = (n => t(n).hash());

export default Builder;

