
import * as R from 'ramda';

export class Cmd { constructor(f) { this.f = f; } }
export function cmd(f) { return new Cmd(f); }
// window.hi = cmd(() => {window.echo("hello")});
export function dmap(d, f) { return Object.fromEntries(Object.entries(d).map(f)); }
// window.dmap = dmap;

export class Unique {
    constructor(name) { this.name = name; }
    toString() { return this.name; }
}

export const allIndexed = R.addIndex(R.all);

export function find(arr, f) {
    for (var x of arr) {
        let y = f(x);
        if (y) { return [x, y]; }
    }
    return [null, null];
}

