import React from 'react';
import './App.css';
import {cmd} from './Util.js';
import Terminal from './Terminal';
import Cytoscape from './Cytoscape.js';
import {Row, Col} from 'react-bootstrap';
import FontAwesome from 'react-fontawesome';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.send = this.send.bind(this);
    this.setupTerm = this.setupTerm.bind(this);
    // this.send("hi");
  }
  send(val) {
    window.terminal.insert(val);
    window.terminal.focus();
  }
  exec(val) {
    window.terminal.exec(val);
    window.terminal.focus();
  }
  setupTerm(term) {
    window.terminal = this.terminal = term;
    window.echo = term.echo;
    window.mecho = (x => { if (window.debug) { term.echo(x) } });
    window.mlog = (x => { if (window.debug) { console.log(x) } });
    window.dbg = cmd(() => window.debug = true);
    window.send =       (x => this.send(x));
    window.exec =       (x => this.exec(x));
    window.focusTerm =  (x => this.focusTerm(x));
    term.focus();
    // Automatically run commands through terminal at start:
    // this.exec("trs()");
    var searchParams = new URLSearchParams(window.location.search);
    if (searchParams.has("exec")) {
      this.exec(searchParams.get("exec"));
    }
  }
  focusTerm() {
    setTimeout(window.terminal.focus, 0)
  }
  render() {
    return (
      <div className="App">
        <Row>
          <Col xs={6} sm={6} md={7} lg={7} style={{"background": "linear-gradient(to bottom right, palegreen, aqua)"}}>
            <div className="App-body">
              <p id="logo" className="App-logo" onClick={()=>{this.send("secret");this.focusTerm()}}>
                <FontAwesome
                  name='mouse-pointer'
                  style={{ transform: 'scale(0.6, 1) rotate(112.5deg)' }} />
                _
              </p>
              <Cytoscape send={this.send} exec={this.exec} focusTerm={this.focusTerm}/>
            </div>
          </Col>
          <Col xs={6} sm={6} md={5} lg={5}>
            <Terminal
              onInit={this.setupTerm}
              greetings={window.greetings}
              prompt="=> " />
          </Col>
        </Row>
      </div>
    );
  }
}

window.secret = cmd(function() {
  var win = window.open("https://youtu.be/I0JobHULJiE", '_blank');
  win.focus();
})

export default App;
