import React from 'react';
import {ReactCytoscape, cytoscape} from 'react-cytoscape';
import klay from 'cytoscape-klay';
import * as R from 'ramda';
import {cmd} from './Util.js';
import * as T from './Term.js';
import * as O from './OrdDBI';
import cxtmenu from 'cytoscape-cxtmenu';

import data from './data2.json';
// import trsData from './data.json';

cytoscape.use(klay);
var klayy = window.klay = { name: 'klay', animate: true, klay: { direction: 'DOWN' } };
var dagre = window.dagre = { name: 'dagre', animate: true, nodeSep: 25, rankSep: 40 };
var tree = window.tree = { name: 'tree', animate: true };
var cose = window.cose = { name: 'cose', animate: true };
var none = window.none = { name: 'none' };

cytoscape.use( cxtmenu );

////

const style = [
  {
    selector: 'node',
    style: {
      'content': 'data(name)',
      'text-valign': 'center',
      'background-color': 'data(color)',
      'border-color': ele => ele.data('border-color'),
      'border-width': ele => ele.data('border-color') ? '2px' : '0',
      'text-outline-color': 'data(color)',
      'text-outline-width': '2px',
      'color': '#FFF',
      'font-size': 9,
      'shape': 'data(shape)',
      'text-wrap': 'wrap',
      'text-margin-y': ele => ele.data('text-margin-y')
    }
  },

  {
    selector: 'edge',
    style: {
      // 'content': 'data(id)',
      'content': 'data(edgename)',
      'font-size': 8,
      'curve-style': 'bezier',
      'width': ele => ele.data('width') || 3,
      'target-arrow-shape': ele => ele.data('identify') ? 'None' : 'triangle',
      'line-color': '#9dbaea',
      'target-arrow-color': '#9dbaea',
      'source-endpoint': 'inside-to-node'
    }
  }
];

class Cytoscape extends React.Component {
  constructor(props) {
    super(props);
    this.send = props.send;
    this.exec = props.exec;
    this.focusTerm = props.focusTerm;
    this.setupCyto = this.setupCyto.bind(this);
    window.focusTerm = this.focusTerm;
  }

  setupCyto(cy) {
    var cyto = this;
    window.cy = cyto.cy = cy;
    window.G = id => cy.$id(String(id));
    // window.cyto = cyto;

    // console.log(data);
    cy.maxZoom(4);

    ////

    function treeLayout() {

      function layout(node) {
        var outgoers = node.outgoers().nodes().sort((e1, e2) => parseInt(e1)-parseInt(e2));
        if (outgoers.empty()) { return [[0, 0]]; }

        var children = R.zip(outgoers, outgoers.map(layout));
        var [node0, shape] = children[0];
        var width = 0;
        for (var [cnode, cshape] of children.slice(1)) {
          width = Math.max(...R.zipWith((L, R) => L[1]-R[0]+1, shape, cshape));
          cnode.scratch().shift = width;
          cshape = R.map(S => [S[0]+width, S[1]+width], cshape);

          var newShape = R.zipWith((L, R) => [L[0], R[1]], shape, cshape);
          if (shape.length > cshape.length) { newShape = newShape.concat(shape.slice(cshape.length)); }
          else if (shape.length < cshape.length) { newShape = newShape.concat(cshape.slice(shape.length)); }
          shape = newShape;
        }

        shape = [[0, 0]].concat(R.map(S => [S[0]-width/2, S[1]-width/2], shape));
        children.map(c => c[0].scratch().shift -= width/2);
        return shape;
      }

      function place(node, pos=null, x=0, y=0) {
        x += node.scratch().shift*60*2/Math.sqrt(3);
        y += 60;
        pos[node.id()] = {x, y};
        node.outgoers().nodes().map(n => place(n, pos, x, y));
      }

      var positions = {};
      if (cy.nodes().nonempty()) {
        cy.nodes().map(function(node) { node.scratch().shift = 0 });
        layout(cy.nodes().roots());
        place(cy.nodes().roots(), positions);
      }

      // console.log(positions);
      cy.layout({ name: 'preset', animate: true, positions }).run();
    }

    var toLayout = window.toLayout =
      (layout) => (typeof layout === 'object' ? layout : { name: layout, animate: true });
    var lay = function(layout) {
      var layout = layout ? toLayout(layout) : cyto.layout || dagre;
      // console.log(layout.name);
      if (layout.name === "tree") { return treeLayout(); }

      var roots = cy.elements().roots();

      var dists = cy.elements().bellmanFord({root: roots});
      const colors = ["#11479E", "#0A1128", "#586F7C", "#EAC435", "#E40066"];
      cy.elements().map(function(n) {
        if (!n.data("forceStyle")) {
          n.data("color", colors[dists.distanceTo(n) % colors.length]);
          n.data("shape", n.data("collapsed") ? "diamond" :
                          n.data("focused") ? "star" : "ellipse");
        }
      })

      var focused   = cy.filter(n => n.data("focused"));
      var collapsed = cy.filter(n => n.data("collapsed"));
      var underAllFocused =
        R.reduce((acc,node) => acc.intersection(node.successors().union(node)),
                 cy.elements(),     // all
                 focused);
      var blockedEdges = collapsed.outgoers().filter(e => e.isEdge());

      // cy.elements().hide();
      cy.elements().show();
      cy.elements().filter(n => n.isNode()).hide();
      cy.elements().not(blockedEdges).breadthFirstSearch({
        root: roots,
        directed: true,
        visit: function(n, e) {
          if (underAllFocused.has(n)) {
            n.show();
          }
        }
      });
      blockedEdges.hide();

      if (layout != none) {
        cy.filter(n => n.visible()).layout(layout).run();
      }
    }
    window.lay = window.l = cmd(lay);

    function maybeLay() { if (cyto.layout) { lay(); } }
    var setLay = window.setLay = function(layout) {
      cyto.layout = toLayout(layout);
      maybeLay();
    };

    function batch() {
      if (cyto.batching) { return; }
      cy.startBatch();
      cyto.batching = true;
      setTimeout(() => {
        cy.endBatch();
        cyto.batching = false;
        maybeLay();
      })
    }
    window.batch = batch;

    ////

    var NEXT_ID = 0;
    function next() { return NEXT_ID++; }

    var add = window.add = window.a = function(source, target) {
      batch();
      var id = next();
      return cy.add({ data:
        source ?
          {id: id, name: id, source: source.id(), target: (target || add()).id()} :
          {id: id, name: id}
      });
    }
    var rem = window.rem = window.r = function() {
      batch();
      Array.from(arguments).map(x => cy.remove(x));
    }

    var spl = window.spl = window.s = function(edge) {
      var mid = add();
      add(edge.source(), mid);
      add(mid, edge.target());
      rem(edge);
      return mid;
    }
    var dia = window.dia = window.d = function(edge) {
      var mid = add();
      add(edge.source(), mid);
      add(mid, edge.target());
      spl(edge);
    }

    ////

    // var n0 = add(), n1 = add(), n2 = add(), n3 = add(), n4 = add(), n5 = add();//, n6 = add();
    // add(n0, n1) && add(n0, n2) && add(n2, n3) && add(n2, n4) && add(n1, n5);// && add(n1, n6);

    // setLay(dagre);
    // var n0 = add(), n1 = add(), n2 = add(), n3 = add(), n4 = add(), n5 = add(), n6 = add();
    // add(n0, n1) && add(n0, n2) && add(n0, n3) && add(n2, n4) && add(n3, n5) && add(n3, n6);
    setLay(dagre);

    // var NODES = {};
    function addData(data) {
    // var addData = window.addData = function(data) {
      // if (data.name && data.name in NODES) { return NODES[data.name]; }
      var n = add();
      if (data.name) {
        // NODES[data.name] = n;
        n.data("name", data.name);
      }
      if (data.collapsed) {
        n.data("collapsed", true);
        n.data("reset-collapsed", true);
      }
      if (data.info) {
        n.data("info", data.info);
      }
      R.map(c => add(n, addData(c)), data.children || []);
      return n;
    }

    // addData(data);
    // setLay(dagre);
    // lay();

    ////

    const applyN = R.compose(R.reduceRight(R.compose, R.identity), R.repeat);
    window.flash = applyN(x => x.animate({style:{opacity:0.5},duration:250})
                                .animate({style:{opacity:1},duration:250}), 4);
    window.grow = function() {
      window.stop = false;
      cy.$().remove(); add();
      (function tick() {
        if (!window.stop) {
          add(cy.nodes().min(Math.random).ele);
          setTimeout(tick, 1000);
        }
      })();
    }

    var hide = window.hide = function(node) {
      batch();
      node.data("collapsed", true);
    }
    var show = window.show = function(node) {
      batch();
      node.data("collapsed", false);
    }
    var focus = window.focus = function(node) {
      batch();
      // cy.elements().map(unfocus);
      node.data("focused", true);
    }
    var unfocus = window.unfocus = function(node) {
      batch();
      node.data("focused", false);
    }

    var toggleShow = window.toggleShow = function(node) {
      node.data("collapsed") ? show(node) : hide(node);
    }
    var toggleFocus = window.toggleFocus = function(node) {
      node.data("focused") ? unfocus(node) : focus(node);
    }
    var collapse = window.collapse = function(node) {
      node.union(node.successors()).map(hide);
    }
    var expand = window.expand = function(node) {
      node.union(node.successors()).map(show);
    }
    var reset = function() {
      cy.elements().map(n => n.data("reset-collapsed") ? hide(n) : show(n));
      cy.elements().map(unfocus);
    }
    window.reset = cmd(reset);

    ////

    var clear = function () {
      cy.remove('*')
    }
    window.clear = cmd(clear);
    var trs = function() {
      clear();
      setLay(none); // setLay(cose);
      cy.userZoomingEnabled(false);
      cy.userPanningEnabled(false);
      document.getElementById('logo').style.display = "none";

      // addData(trsData);
      var B = window.B = new T.Builder(cy, next);
      window.ireduce = (...args) => B.ireduce(...args);
      window.iprove  = (...args) => B.iprove(...args);
      window.reduce  = (...args) => B.reduce(...args);
      window.prove   = (...args) => B.prove(...args);
      window.areduce = (...args) => B.areduce(...args);
      window.aprove  = (...args) => B.aprove(...args);
      window.r       = (...args) => B.r(...args);
      window.use     = (...args) => B.use(...args);
      window.iequate = (...args) => B.iequate(...args);
      window.isteps  = (...args) => B.isteps(...args);
      // window.test    = (...args) => B.test(...args);
      
      let term = T.ifte(
          T.ifte(T.prim(true), T.prim(false), T.prim(true)), 
          T.prim(3),      // T.plus(T.prim(3), T.mul(T.prim(3), T.prim(5))), 
          T.plus(T.prim(3), T.prim(5)));
      // let term = T.ifte(T.prim(true), T.prim(10), T.prim(20));
      // term = T.quote(term);
      B.add(term);

      // let t0 = O.app(O.chop(2, O.VAR), T.prim(true))
      // let env = T.prim(null);
      // env = O.link(T.prim('b'), 1, env);
      // env = O.link(T.prim('a'), 1, env);
      // env = O.link(t0, 1, env);
      // let t1 = O.lamb(env);
      // B.add(t1);

      // B.add(T.mul(T.prim(4), T.type("foo")));

      // let t = O.app(O.v(1), T.prim(123));
      // // let t = O.app(O.chop(1, T.prim(456)), T.prim(123));
      // t = O.cut(1, t);
      // B.add(t);

      return "TRS initiated. Enter [[;limegreen;]trshelp] for help and additional commands."
    }
    window.trs = cmd(trs);

    trs();

    ////

    cy.on('tap', 'node, edge', function(evt) {
      var e = evt.originalEvent;
      // console.log(e);
      if (e.altKey) {
        if (e.shiftKey) {
          cyto.exec(`r(G(${this.id()}))`);
        } else if (e.ctrlKey || e.metaKey) {
          cyto.exec(`ireduce(G(${this.id()}))`);
        } else {
          cyto.exec(`iprove(G(${this.id()}))`);
        }
      } else if (e.shiftKey) {
        cyto.exec(`toggleFocus(G(${this.id()}))`);
      } else if (e.ctrlKey || e.metaKey) {
        cyto.exec(`toggleShow(G(${this.id()}))`);
      } else {
        cyto.send(`G(${this.id()})`);
      }
    });
    cy.on('tapstart tapend cxttapstart cxttapend', window.focusTerm);

    let commands = [ // reverse order
      { content: 'rem',      select: ele => window.exec(`r(G(${ele.id()}))`),             fillColor: 'rgba(50, 0, 0, 0.75)', },
      { content: 'focus',    select: ele => window.exec(`toggleFocus(G(${ele.id()}))`),   fillColor: 'rgba(50, 0, 0, 0.75)', },
      { content: 'show',     select: ele => window.exec(`toggleShow(G(${ele.id()}))`),    fillColor: 'rgba(50, 0, 0, 0.75)', },
      { content: '*',       select: ele => window.exec(`isteps(G(${ele.id()}))`),        fillColor: 'rgba(0, 0, 50, 0.75)', },
      { content: '=',        select: ele => window.exec(`iequate(G(${ele.id()}))`),       fillColor: 'rgba(0, 0, 50, 0.75)', },
      { content: '',        select: ele => window.exec(`ireduce(G(${ele.id()}))`),       fillColor: 'rgba(0, 0, 50, 0.75)', },
      { content: 'a',      select: ele => window.exec(`aprove(G(${ele.id()}))`),         fillColor: 'rgba(0, 50, 0, 0.75)', },
      { content: 'i',      select: ele => window.exec(`iprove(G(${ele.id()}))`),         fillColor: 'rgba(0, 50, 0, 0.75)', },
      // { content: '',             select: ele => window.exec(`prove(G(${ele.id()}))`) },
    ]
    // let L = commands.length;
    // for (let [i, cmd] of commands.entries()) {
    //   cmd.contentStyle = { transform: `rotate(${-Math.PI*((2*i+1)/L+(i<L/2 ? -0.5 : 0.5))}rad)` }
    // }
    cy.cxtmenu({commands});

  }

  render() {
    return (
      <ReactCytoscape containerID="cy"
        cyRef={this.setupCyto}
        elements={{nodes:[]}}
        // layout={tree}
        style={style}
        cytoscapeOptions={{wheelSensitivity: 0.1}}/>
    );
  }

}

export default Cytoscape;
